.customize-control-cssMargin .customize-control-content {
    > div {
        clear: both;

        > div {
            float: left;
            width: 50px;
            min-height: 30px;
            margin: 4px;
            text-align: center;

            .dashicons {
                font-size: 30px;
                width: auto;
                height: auto;
            }

            input {
                padding: 0 0 0 8px;
            }
        }
    }
}
